html {
  height: unset;
}

li {
  cursor: default;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
  color: $textColor;

  &.overflow {
    overflow: hidden;
  }
}
.wow{
  visibility: hidden;
}
.section{
  overflow: hidden;
}
.bold {
  font-weight: 600;
  color: #888;
  text-transform: uppercase;
}

.container {
  max-width: 1170px;
  width: 100%;
  margin: 0 auto;
}

.flex-start {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.section__img {
  display: flex;
  justify-content: center;
  align-items: center;
}

.logo-img-big {
  width: 70%;
}

.custom-list {
  display: flex;
  flex-direction: column;

  &__item {
    font-family: "Montserrat", sans-serif;
    font-weight: 700;
    font-size: 16px;
    letter-spacing: 1.01px;
  }

  &__item+&__item {
    margin-top: 15px;
  }

  &.arrow-type {
    li {
      position: relative;

      &::before {
        content: "\f054";
        color: $primaryColor;
        margin-right: 15px;
        font-family: FontAwesome;
      }
    }
  }

  &.underline-type {
    .custom-list__item {
      position: relative;
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
      flex-direction: column;

      .toggle-link {
        font-size: 18px;
        width: 100%;
        color: $textColor;
        padding-right: 15px;
        transition: 0.3s all ease-in-out;
        padding-bottom: 5px;
        border-bottom: 2px solid $primaryColor;

        &::after {
          position: absolute;
          right: 0;
          content: "\f149 ";
          font-family: FontAwesome;
          color: black;
        }

        &:hover {
          border-bottom-color: black;
        }

        &.active {
          border-bottom-color: black;
        }
      }
    }
  }
}

.btn {
  display: flex;
  background: $primaryColor;
  color: white;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  text-transform: uppercase;
  position: relative;

  a {
    color: black;
    position: relative;
    z-index: 3;
    transition: 0.5s all ease-in-out;
  }

  &-two {
    width: 300px;
    height: 50px;
    background: none;
    border: 2px solid $primaryColor;
    color: black;
    overflow: hidden;
    transition: 0.5s all ease-in-out;

    &:after {
      content: "";
      display: block;
      position: absolute;
      left: 0;
      top: 0;
      width: 0;
      height: 100%;
      background: $primaryColor;
      transition: 0.3s all ease-in-out;
      z-index: 0;
    }

    &:hover {
      color: white;

      &:after {
        width: 100%;
      }

      a {
        color: white;
      }
    }
  }

  &__link {
    color: white;
    font-size: 16px;
    font-family: "Open Sans", sans-serif;
    font-weight: 600;
    letter-spacing: 1.01px;
  }

  &-arrow {
    background: none;
    width: 300px;
    height: 50px;
    border: 2px solid transparent;
    border-bottom-color: $primaryColor;
    transition: 0.3s all ease-in-out;

    &::after {
      position: absolute;
      font-family: FontAwesome;
      content: "\f061";
      right: 50px;
      opacity: 0;
      transition: 0.3s all;
      color: black;
    }

    &:hover {
      border-color: $primaryColor;

      &::after {
        opacity: 1;
        right: 13px;
      }
    }
  }
}

.sub-list {
  margin-top: 0px;
  padding-left: 15px;

  &.active {
    margin-top: 15px;
  }

  &__item {
    font-family: "Open Sans", sans-serif;
    font-weight: 600;

    &::before {
      font-family: FontAwesome;
      content: "\f105 ";
      color: $primaryColor;
      margin-right: 15px;
    }
  }

  &__item+&__item {
    margin-top: 5px;
  }
}

.toggle-link {
  z-index: 6;
}

.toggle-block {
  display: none;
  transition: 0.5s all ease;

  &.active {
    position: relative;
    opacity: 1;
    z-index: 5;
    transform: translateX(0);
  }
}

.number {
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
}

.price-wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.price-card {
  display: flex;
  flex-direction: column;
  background-size: cover;
  width: 300px;
  justify-content: flex-start;
  align-items: center;
  position: relative;

  &:nth-child(2n) {
    .price-card__title {
      background-color: rgba(#888, 0.2);
    }

    .price-card__info {
      background-color: $primaryColor;
    }
  }

  &__title {
    font-size: 26px;
    text-align: center;
    font-weight: 500;
    color: black;
    background-color: $primaryColor;
    width: 100%;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__info {
    padding: 15px;
    width: 100%;
    background-color: rgba(#888, 0.2);
  }
}

.social-list {
  display: flex;

  &-mobile {
    display: none;
  }

  &__item+&__item {
    margin-left: 15px;
  }

  &__item {
    display: flex;
    align-items: center;
    justify-content: center;

    .fa {
      font-size: 22px;
      color: white;
    }
  }
}

.logo-mobile {
  display: none;
  align-items: center;
  justify-content: center;

  img {
    height: 32px;
  }
}

.map {
  width: 100%;
  height: 100vh;
}

.level-link {
  cursor: pointer;
}

.level-content {
  background: rgba($color: #000000, $alpha: .8);
  padding: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  z-index: -1;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  position: fixed;
  opacity: 0;
  transition: 0.5s all linear;

  &.active {
    opacity: 1;
    z-index: 9999;
  }

  .level-list {
    background-color: white;
    width: 800px;
    padding: 0 15px;
    padding-top: 15px;
  }
}

.level-list__item {
  text-align: left;
  display: flex;
  align-items: center;
  border-bottom: 1px solid $primaryColor;
  margin: 5px 0;
  padding-bottom: 5px;

  &:first-child {
    border: none;
    text-align: center;
    flex-basis: unset;
  }

  &:nth-child(2) {
    border-top: 1px solid $primaryColor;
    padding-top: 5px;
  }

  &:last-child {
    border: none;
    margin-top: 15px;
  }

  .level-list__desc {
    flex-basis: 100%;
    transition: 0.5s all ease;
    text-align: left;
    font-weight: 400;
    font-size: 15px;
    line-height: 17px;
  }
}

.level-content__title {
  font-size: 20px;
  text-align: center;
  width: 100%;
  margin-bottom: 10px;
}

.level-list__title {
  flex-basis: 25%;
  padding-left: 5px;
  text-align: center;

  &.level-list__title-big {
    flex-basis: 100%;
    padding: 10px 0;
  }
}

.form__label {
  display: flex;
  justify-content: flex-start;
  align-items: center;

  .level-link {
    margin-left: 10px;
    font-size: 16px;

    .fa {
      color: grey;
    }

    &:hover {
      color: #343434;

      .fa {
        color: #343434;
      }
    }
  }
}

@media screen and(max-width : 768px) {
  .level-list {
    height: 500px;
    overflow: scroll;
  }

  .welcome {
    .logo {
      img {
        width: 100%;
      }
    }
  }
}

@media screen and (max-width:375px) {
  .btn-two {
    width: 260px;
  }
}