.contact {
  width: 100%;
  padding: 0 50px;
}

.contact__block {
  display: flex;

  &+.contact__block {
    margin-top: 30px;
  }

  >div+div {
    margin-left: 15px;
  }
}

.operating {
  line-height: 30px;
}

.contact__img {
  .fa {
    font-size: 30px;
  }
}

.contact__content {
  font-size: 15px;
  font-weight: 600;

  p {
    text-align: justify;
  }
}

.phone-list__item {
  &+.phone-list__item {
    margin-top: 7px;
  }

  a {
    color: $textColor;
    text-decoration: underline;
  }
}

.operating_mode {
  p+p {
    margin-top: 5px;
  }

  span {
    color: $textColor;
  }

  .week-day {
    font-weight: 700;
    border-bottom: 2px solid $primaryColor;
    font-size: 16px;
  }

  .number {
    font-family: "Montserrat", sans-serif;
    font-weight: 700;
    font-size: 16px;
    border-bottom: 2px solid $primaryColor;
  }
}

.contact {
  .social-list {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin-top: 30px;
  }

  .social-list__item {
    &+.social-list__item {
      margin-left: 30px;
    }

    .fa {
      font-size: 36px;
      transition: 0.3s all ease-in-out;
      color: $textColor;
    }
  }
}

.fa {
  color: $textColor;

  &-telegram {
    &:hover {
      color: #2096d4;
    }
  }

  &-instagram {
    &:hover {
      color: #8a41d0;
    }
  }

  &-facebook-official {
    &:hover {
      color: #1d4d99;
    }
  }
}

.contact__address {
  display: inline-block;
  font-style: normal;
  font-weight: 700;
}

.form-feedback {
  width: 100%;

  .btn-row {
    padding-top: 0;
  }
}

.form-feedback__row {
  &+.form-feedback__row {
    margin-top: 15px;
  }

  display: flex;

  >div {
    flex: 1 1;
  }

  .btn__wrapper {
    margin-top: 15px;

    &.right-left {
      &::after {
        transform: translate(0, 100%);
      }

      &:hover {
        &::after {
          transform: translate(0, 0);
        }
      }
    }
  }
}

.form-feedback__input {
  display: flex;
  flex-direction: column;

  &+.form-feedback__input {
    margin-left: 10px;
  }

  label {
    margin-bottom: 10px;
    font-family: "Open Sans", sans-serif;
    font-weight: 600;
  }

  input,
  select,
  option {
    height: 40px;
    padding: 0 15px;
    font-weight: 500;
    font-family: "Montserrat", sans-serif;
    font-size: 15px;
  }

  input,
  select {
    box-shadow: 0 1px 2px 1px rgba(0, 0, 0, 0.15);
  }
}

.form-feedback__select {
  option {
    height: 50px;
  }
}

@media all and(max-width:490px) {
  .form-feedback__row {
    flex-direction: column;

    .btn__wrapper {
      flex: unset;
    }
  }
  .contact{
    padding:0 30px;
  }

  .form-feedback__input {
    &+.form-feedback__input {
      margin: 0;
      margin-top: 15px;
    }
  }
}
@media all and (max-width:320px){
  .contact{
    padding:0;
  }
}