#photo-section{
    .section__row{
        display: flex;
        &.section__content{
            padding-top:60px;
        }
    }
    img{
        width: 100%;
        
        margin-bottom: 15px;
    }
    address{
        text-align: center;
        font-size: 16px;
    }
    .text{
        font-size: 16px;
        font-family: 'Montserrat', sans-serif;
        font-weight: 700;
        margin:15px 0;
        letter-spacing: 1.01px;
    }
}
@media screen and (max-width:768px){
    #photo-section{
        img{
         height: 450px;
        }
    }
}
@media screen and (max-width:600px){
    #photo-section{
        padding-left: 30px;
        padding-right: 30px;
        img{
            height: auto;
            width: 100%;
        }
        .section__sub-title{
            p{
                font-size: 12px;
            }
        }
    }
}